.SearchInputContainer{
    width: 100%;
    height: 64px;
    position: relative;
}

.SearchInput{
    position: absolute;
    bottom: 2px;
    left: 25px;
    height: 30px;
    width: 200px;
    border: 1px solid #a5a5a5;
    border-radius: 10px;
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold;
}
.SearchInput::placeholder{
    color: #a5a5a5;
    font-size: 1.2rem;
    text-align: center;
}
